@import 'variables';

.catalog {
  --column-count: 1;

  padding: 50px 40px;

  &__grid {
    display: grid;
    grid-template-columns: repeat(var(--column-count), $card-width);
    gap: 46px 48px;
    justify-content: center;

    @media (min-width: 488px) {
      --column-count: 2;
    }

    @media (min-width: 768px) {
      --column-count: 3;
    }

    @media (min-width: 1024px) {
      --column-count: 4;
    }

    @media (min-width: 1200px) {
      --column-count: 4;
    }
  }
}
