* {
  box-sizing: border-box;
}

body {
  flex-direction: column;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card p, .card h2 {
  margin: 0;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  padding-bottom: 16px;
  font-size: 10px;
}

.card__rating-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy-button {
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  gap: 4px;
  display: flex;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 .star:first-child, .stars--2 .star:nth-child(-n+2), .stars--3 .star:nth-child(-n+3), .stars--4 .star:nth-child(-n+4), .stars--5 .star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  --column-count: 1;
  padding: 50px 40px;
}

.catalog__grid {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__grid {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog__grid {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog__grid {
    --column-count: 4;
  }
}

@media (width >= 1200px) {
  .catalog__grid {
    --column-count: 4;
  }
}
